body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  background-color: var(--container-color);
  color: #fff; /* All text white */
  margin-bottom: 20px;
}

/* Feedback Section */
.feedback {
  padding: 2rem;
  max-width: 600px;
  margin: 2rem auto;
  background: #111; /* Dark F1 vibe */
  box-shadow: 0 5px 20px rgba(255, 69, 0, 0.3); /* Neon F1 shadow */
  border-radius: 1.5rem;
  position: relative;
}

/* Section Title */
.feedback__title {
  font-size: 1.8rem;
  font-weight: bold;
  margin-bottom: 1.5rem;
  text-align: center;
  color: #ff4500; /* F1 accent color */
  text-transform: uppercase;
  letter-spacing: 1px;
}

/* Form Styling */
.feedback form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.feedback__input,
.feedback__textarea {
  width: 100%;
  padding: 0.8rem 1rem;
  border: 1px solid #333;
  border-radius: 0.75rem;
  font-size: 1rem;
  background-color: #222; /* Dark input field */
  color: #fff;
  box-shadow: inset 0 2px 5px rgba(0,0,0,0.5);
  transition: all 0.3s ease;
}

.feedback__textarea {
  resize: none;
  min-height: 150px;
}

.feedback__input:focus,
.feedback__textarea:focus {
  outline: none;
  border-color: #ff4500;
  box-shadow: 0 0 8px rgba(255, 69, 0, 0.7);
}

/* Submit Button */
.feedback__button {
  padding: 0.8rem 1rem;
  background-color: #ff4500; /* F1 accent */
  color: #fff;
  font-size: 1rem;
  font-weight: bold;
  border: none;
  border-radius: 0.75rem;
  cursor: pointer;
  text-transform: uppercase;
  transition: all 0.3s ease;
}

.feedback__button:hover {
  background-color: #ff6347;
  box-shadow: 0 0 10px #ff6347, 0 0 20px rgba(255, 99, 71, 0.4);
}

.feedback__button--loading {
  background-color: #555;
  cursor: not-allowed;
}

.feedback button:disabled {
  background-color: #444;
  cursor: not-allowed;
}

/* Toast Notifications */
.Toastify__toast--success {
  background: #28a745; /* green success */
  color: #fff;
}

.Toastify__toast--error {
  background: #dc3545; /* red error */
  color: #fff;
}

.Toastify__toast-icon {
  width: 20px !important;
  height: 20px !important;
  margin-right: 10px;
}

.Toastify__toast-body {
  display: flex;
  align-items: center;
  padding: 10px 15px;
}

.Toastify__toast {
  padding: 10px 20px;
  border-radius: 10px;
  box-shadow: 0 5px 15px rgba(0,0,0,0.3);
}

/* Responsive Design */
@media screen and (max-width: 768px) {
  .feedback {
    padding: 1.5rem;
    margin: 1rem;
  }

  .feedback__title {
    font-size: 1.5rem;
  }
}
