.tabs {
  height: 100%;
  display: flex;
  position: relative;
  font-family: 'Arial', sans-serif;
  gap: 1rem;
}

/* Tab List (Left Menu) */
.tab__list {
  width: 15rem;
  margin-left: 5px;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

/* Each Tab Button */
.button {
  display: inline-block;
  width: 100%;
  padding: 1rem 1.5rem;
  background: #1a1a1a; /* F1 dark panel */
  cursor: pointer;
  font-size: var(--normal-font-size);
  font-weight: var(--font-bold);
  color: #fff;
  text-align: left;
  border-radius: 12px;
  transition: all 0.3s ease;
}

.button:hover {
  background: linear-gradient(90deg, #ff0000, #ff4500); /* racing gradient highlight */
  color: #fff;
}

.tab.is-active .button {
  background: linear-gradient(90deg, #ff0000, #ff6347);
  color: #fff;
  box-shadow: 0 5px 15px rgba(0,0,0,0.4);
}

/* Tab Panel Container */
.tab__panel {
  flex: 1;
  display: none;
  padding: 1.5rem 2rem;
  background-color: #1a1a1a; /* dark dashboard panel */
  border-radius: 12px;
  box-shadow: 0 5px 20px rgba(0,0,0,0.3);
  color: #fff;
}

.tab__panel.is-active {
  display: block;
  border-radius: 12px;
}

/* Tab Panel Titles and Subtitles */
.tab__panel-title {
  font-size: var(--h3-font-size);
  margin-bottom: 1rem;
  font-weight: bold;
  color: #fff;
}

.tab__panel-subtitle {
  font-size: var(--normal-font-size);
  margin-bottom: 1rem;
  color: #ccc;
}

/* Panel List Items */
.tab__panel-list li {
  position: relative;
  padding-left: 20px;
  margin-bottom: 0.5rem;
}

.tab__panel-list li::before {
  content: "▹";
  position: absolute;
  left: 0;
  color: #ff4500; /* small accent */
}

/* Responsive Adjustments */
@media screen and (max-width: 768px) {
  .tabs {
    flex-wrap: wrap;
  }

  .tab__list {
    width: 100%;
    margin-bottom: 1rem;
  }

  .tab.is-active .button {
    border-radius: 12px;
  }
}
