/* Filter styling */
.portfolio__filters {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  column-gap: 1.5rem;
  margin-bottom: 2.5rem;
  justify-content: center;
}

/* Filter items */
.portfolio__item {
  cursor: pointer;
  font-weight: var(--font-bold);
  text-transform: uppercase;
  letter-spacing: 0.5px;
  padding: 0.5rem 1rem;
  transition: 0.3s;
  border-radius: 10px;
  background-color: rgba(255, 255, 255, 0.05);
}

.portfolio__item-active,
.portfolio__item:hover {
  color: #ff4500; /* Neon F1 accent */
  background-color: rgba(255, 69, 0, 0.2);
  box-shadow: 0 0 8px #ff4500, 0 0 16px #ff6347;
}

/* Portfolio container grid layout */
.portfolio__container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1.5rem;
  justify-items: center;
}

/* Portfolio card */
.portfolio__card {
  position: relative;
  overflow: hidden;
  border-radius: 15px;
  width: 100%;
  height: 280px; /* Fixed card height */
  box-shadow: 0 5px 15px rgba(0,0,0,0.3);
  transition: transform 0.3s, box-shadow 0.3s;
  background-color: #111; /* Fallback for dark racing theme */
}

.portfolio__card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 25px rgba(255,69,0,0.5);
}

/* Image styling */
.portfolio__card img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
  border-radius: 15px;
  transition: transform 0.5s;
}

.portfolio__card:hover img {
  transform: scale(1.1);
}

/* Hover overlay / mask */
.portfolio__mask {
  position: absolute;
  inset: 0;
  background: linear-gradient(45deg, rgba(255,0,0,0.6), rgba(255,69,0,0.6));
  opacity: 0;
  transition: 0.3s ease-in-out;
  border-radius: 15px;
}

.portfolio__card:hover .portfolio__mask {
  opacity: 0.9;
}

/* Category label */
.portfolio__category {
  position: absolute;
  top: 1rem;
  left: 1rem;
  padding: 0.25rem 0.75rem;
  font-size: var(--small-font-size);
  font-weight: bold;
  background-color: #ff4500;
  color: #fff;
  border-radius: 5px;
  transform: translateY(-20px);
  opacity: 0;
  transition: 0.3s;
}

.portfolio__card:hover .portfolio__category {
  opacity: 1;
  transform: translateY(0);
}

/* Portfolio title */
.portfolio__title {
  position: absolute;
  bottom: 3rem;
  left: 1rem;
  font-size: var(--h3-font-size);
  font-weight: bold;
  color: #fff;
  opacity: 0;
  transform: translateY(20px);
  transition: 0.3s;
}

.portfolio__card:hover .portfolio__title {
  opacity: 1;
  transform: translateY(0);
}

/* GitHub button / icon */
.portfolio__github-button {
  position: absolute;
  bottom: 1rem;
  left: 1rem;
  width: 40px;
  height: 40px;
  line-height: 40px;
  font-size: 1.2rem;
  color: #111;
  background-color: #ffd15c;
  border-radius: 50%;
  text-align: center;
  cursor: pointer;
  opacity: 0;
  transition: 0.3s;
}

.portfolio__card:hover .portfolio__github-button {
  opacity: 1;
  transform: scale(1.1);
}

/* Media queries for responsiveness */
@media screen and (max-width: 1024px) {
  .portfolio__container {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media screen and (max-width: 768px) {
  .portfolio__container {
    grid-template-columns: 1fr;
  }

  .portfolio__filters {
    justify-content: center;
    gap: 0.5rem;
  }
}
