.services__container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1.875rem;
  margin-top: 2rem;
}

/* Service Card Styling */
.services__card {
  text-align: center;
  padding: 2rem 1.5rem;
  border-radius: 1.5rem;
  background-color: #111; /* Dark F1 cockpit vibe */
  box-shadow: 0 5px 15px rgba(255, 69, 0, 0.3); /* Neon accent shadow */
  transition: all 0.3s ease-in-out;
  cursor: pointer;
  border: 2px solid rgba(255, 255, 255, 0.05);
}

.services__card:hover {
  transform: translateY(-10px);
  box-shadow: 0 8px 25px rgba(255, 69, 0, 0.6), 0 0 15px rgba(255, 69, 0, 0.3);
}

/* Card Image */
.services__img {
  margin-bottom: 1.25rem;
  width: 80px;
  height: 80px;
  filter: brightness(1.2);
}

/* Card Title */
.services__title {
  font-size: 1.4rem;
  font-weight: bold;
  margin-bottom: 0.75rem;
  color: #ff4500; /* F1 accent color */
  text-transform: uppercase;
  letter-spacing: 1px;
}

/* Card Description */
.services__description {
  font-size: 1rem;
  color: #fff;
  line-height: 1.6;
}

/* Responsive Layouts */
@media screen and (max-width: 1024px) {
  .services__container {
    grid-template-columns: repeat(2, 1fr);
    justify-content: center;
  }
}

@media screen and (max-width: 768px) {
  .services__container {
    grid-template-columns: 1fr;
    justify-content: center;
  }
}
