/* Base font */
.tabs {
  display: flex;
  gap: 1.5rem;
  font-family: 'Poppins', sans-serif;
}

/* Tab list */
.tab__list {
  width: 16rem;
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

/* Buttons */
.button {
  width: 100%;
  padding: 1rem 1.5rem;
  background: transparent;
  border: none;
  border-radius: 14px;
  cursor: pointer;

  font-size: 0.95rem;
  font-weight: 500;
  color: var(--title-color);
  text-align: left;

  transition: all 0.3s ease;
}

.button:hover {
  background: rgba(0, 0, 0, 0.04);
  color: var(--first-color);
}

/* Active tab */
.tab.is-active .button {
  background: var(--container-color);
  color: var(--first-color);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.08);
}

/* Panels */
.tab__panel {
  flex: 1;
  display: none;
  padding: 2rem;
  background: var(--container-color);
  border-radius: 18px;
  box-shadow: 0 12px 40px rgba(0, 0, 0, 0.08);
}

.tab__panel.is-active {
  display: block;
}

/* Panel content */
.tab__panel-title {
  font-size: 1.25rem;
  font-weight: 600;
  margin-bottom: 0.75rem;
}

.tab__panel-subtitle {
  font-size: 0.95rem;
  opacity: 0.8;
  margin-bottom: 1rem;
}

/* List */
.tab__panel-list li {
  padding-left: 1.25rem;
  position: relative;
}

.tab__panel-list li::before {
  content: "•";
  position: absolute;
  left: 0;
  color: var(--first-color);
}

/* Dark mode */
[data-theme="dark"] .tab__panel-title,
[data-theme="dark"] .button {
  color: #fff;
}

/* Mobile */
@media (max-width: 768px) {
  .tabs {
    flex-direction: column;
  }

  .tab__list {
    width: 100%;
    flex-direction: row;
    overflow-x: auto;
  }

  .button {
    text-align: center;
    white-space: nowrap;
  }
}
