/*=============== About Section Styling ===============*/

/* Container Grid */
.about__container {
  display: grid;
  grid-template-columns: 3fr 9fr;
  column-gap: 1.875rem;
}

/* About Data Card */
.about__data {
  padding: 1.875rem;
  background-color: var(--container-color);
  box-shadow: var(--shadow);
  border-radius: var(--border-radius);
  display: grid;
  grid-template-columns: 1fr;
  column-gap: 1.875rem;
  align-items: flex-start;
  position: relative;
}

/* Triangle Pointer for About Card */
.about__data::before {
  content: "";
  width: 0;
  height: 0;
  border-top: 10px solid transparent;
  border-bottom: 10px solid transparent;
  border-right: 15px solid var(--container-color);
  position: absolute;
  left: -0.93rem;
  top: 20%;
}

/* About Description */
.about__description {
  margin-bottom: 1rem;
  text-align: justify;
  font-family: Arial, sans-serif;
  line-height: 1.6;
  color: white;
}

/* Highlight Classes */
.highlight-primary {
  font-weight: bold;
  color: #007ACC;
}
.highlight-secondary {
  font-weight: bold;
  color: #28A745;
}
.highlight-accent {
  font-weight: bold;
  color: #FF4500;
}
.highlight-tertiary {
  font-weight: bold;
  color: #DC143C;
}
.highlight-gold {
  font-weight: bold;
  color: #FFD700;
}
.highlight-info {
  font-weight: bold;
  color: #20B2AA;
}
.highlight-process {
  font-weight: bold;
  color: #8A2BE2;
}
.highlight-tech {
  font-weight: bold;
  color: #4682B4;
}
.highlight-insights {
  font-weight: bold;
  color: #FF6347;
}

/* Skills Section */
.about__skills {
  display: flex;
  justify-content: space-between;
  margin-top: 2rem;
  gap: 2rem;
  flex-wrap: wrap;
}

.skills__column {
  flex: 1;
}

.skills__name {
  font-size: var(--normal-font-size);
  font-weight: var(--font-medium);
  margin-bottom: 1rem;
  color: white;
}

.skills__list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.skills__list li {
  font-size: 1rem;
  line-height: 1.8;
  color: white;
  margin-bottom: 0.5rem;
  position: relative;
  padding-left: 20px;
}

.skills__list li::before {
  content: "▹";
  position: absolute;
  left: 0;
}

/* Skill Bars */
.skills__titles {
  display: flex;
  justify-content: space-between;
  margin-bottom: 1rem;
}

.skills__bar,
.skills__percentage {
  height: 7px;
  border-radius: 0.25rem;
}

.skills__bar {
  background-color: #f1f1f1;
}

.skills__percentage {
  display: block;
}

.development {
  width: 90%;
  background-color: rgb(255, 209, 92);
}

.ui__design {
  width: 80%;
  background-color: rgb(255, 76, 96);
}

.photography {
  width: 60%;
  background-color: rgb(108, 108, 255);
}

/* About Boxes (Achievements / Stats) */
.about__boxes {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  column-gap: 1.875rem;
  margin-top: 4.35rem;
}

.about__box {
  display: flex;
  column-gap: 1.5rem;
  align-items: center;
}

.about__icon {
  font-size: var(--h1-font-size);
  color: #dedeea;
}

.about__title {
  font-size: 1.875rem;
}

/* CV Download Button */
.cv__download {
  margin-top: 2rem;
  text-align: center;
}

.cv__download .btn {
  padding: 0.8rem 1.5rem;
  font-size: 1rem;
  background-color: #007ACC;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.cv__download .btn:hover {
  background-color: #005F99;
}

/* About List (if used) */
.about__list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  margin-bottom: 1rem;
}

/* Responsive Styling */
@media screen and (max-width: 1024px) {
  .about__container {
    grid-template-columns: 720px;
    justify-content: center;
    row-gap: 1.875rem;
  }

  .about__data::before {
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    border-bottom: 10px solid var(--container-color);
    left: 49%;
    top: -20px;
  }

  .about__img {
    justify-self: center;
  }

  .about__box {
    flex-direction: column;
    row-gap: 0.5rem;
    text-align: center;
    align-items: center;
  }
}

@media screen and (max-width: 768px) {
  .about__container {
    grid-template-columns: 350px;
  }

  .about__data::before {
    left: 47%;
  }

  .about__data {
    grid-template-columns: 1fr;
    row-gap: 1.875rem;
  }

  .about__boxes {
    grid-template-columns: repeat(2, 150px);
    row-gap: 1.5rem;
    justify-content: center;
  }
}

@media screen and (max-width: 576px) {
  .about__container {
    grid-template-columns: 320px;
  }
}

@media screen and (max-width: 350px) {
  .about__container {
    grid-template-columns: 1fr;
  }

  .about__boxes {
    grid-template-columns: 1fr;
    align-items: center;
  }
}
/* Container for F1-style skills blocks */
.about__skills-block {
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
  margin-top: 2rem;
  justify-content: center;
}

/* Individual skill block */
.skills-block {
  background-color: #1c1c1c; /* dark racing theme */
  padding: 1.5rem;
  border-radius: 10px;
  flex: 1 1 300px;
  max-width: 400px;
  box-shadow: 0 5px 15px rgba(0,0,0,0.3);
}

/* Skill block title */
.skills-block__title {
  font-size: 1.2rem;
  font-weight: bold;
  color: #fff;
  margin-bottom: 1rem;
  border-bottom: 2px solid #fff;
  padding-bottom: 0.5rem;
}

/* Each skill item */
.skills-block__item {
  margin-bottom: 1rem;
}

/* Skill label (name + percent) */
.skills-block__label {
  display: flex;
  justify-content: space-between;
  font-size: 0.95rem;
  font-weight: bold;
  color: #fff;
  margin-bottom: 0.25rem;
}

/* Skill meter (background bar) */
.skills-block__meter {
  background-color: #333;
  border-radius: 5px;
  height: 12px;
  overflow: hidden;
}

/* Skill fill (dynamic width) */
.skills-block__fill {
  background: linear-gradient(90deg, #fff 0%, #ff0000 70%); /* white to red gradient */
  height: 100%;
  border-radius: 5px;
  transition: width 1s ease-in-out;
}
