.main {
  margin-left: 110px;
}

@media screen and (max-width: 1024px) {
  .main {
    margin-left: 0;
  }
}
:root {
  --bg-main: #0f1115;
  --bg-card: #161a22;
  --text-main: #e5e7eb;
  --text-muted: #9ca3af;
  --accent: #e10600; /* racing red */
  --border-soft: rgba(255, 255, 255, 0.08);
  --shadow-soft: 0 10px 30px rgba(0, 0, 0, 0.5);
}

body {
  margin: 0;
  font-family: 'Inter', system-ui, sans-serif;
  background-color: var(--bg-main);
  color: var(--text-main);
}
